/* Dropdown Button */
.dropbtn {
  background-image: url("../../../../blueberry/public/img/icons/menu_icon.png");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
  background-color: #ffffff;
  color: white;
  margin: 0.5rem;
  padding: 0.75rem;
  font-size: 100%;
  border: none;
  cursor: pointer;
}

/* Dropdown button on hover & focus */
.dropbtn:hover, .dropbtn:focus {
  background-color: #ffffff;
}

/* The container <div> - needed to position the dropdown content */
.dropdown {
  position: relative;
  display: inline-block;
}

/* Dropdown Content (Hidden by Default) */
.dropdown-content {
  z-index: 1;
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  width: auto;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
}

/* Links inside the dropdown */
.dropdown-content a {
  color: black;
  min-width: 5rem;
  padding: 0.5rem 1rem;
  text-decoration: none;
  display: block;
  font-size: 80%;
}

/* Change color of dropdown links on hover */
.dropdown-content a:hover {
  background-color: #f1f1f1;
}

/* Show the dropdown menu (use JS to add this class to the .dropdown-content container when the users clicks on the dropdown button) */
.show {
  display: block;
}

.login {
  width: 25rem;
  margin: 0 auto;
  margin-top: 2rem;
}

.login form {
  width: 25rem;
}

.login form fieldset {
  padding: 2rem;
  text-align: center;
  background: #F9F9F9;
  border: 1px solid #6BBE92;
}

.login form fieldset legend {
  font-size: 90%;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
}

.login form fieldset label {
  width: 22rem;
  display: block;
  margin-top: 1rem;
  padding: 0.3rem;
  font-size: 80%;
  background: #C4D5DB;
  border-bottom: 1px solid #9CAAAF;
}

.login form fieldset input[type="text"],
.login form fieldset input[type="email"],
.login form fieldset input[type="password"] {
  width: 21rem;
  display: block;
  margin: 0 0 0.25rem 0.25rem;
  padding: 0.5rem;
  background: #C0D9D9;
  border: 1px solid #C4D5DB;
}

.login form fieldset input[type="text"]::-webkit-input-placeholder,
.login form fieldset input[type="email"]::-webkit-input-placeholder,
.login form fieldset input[type="password"]::-webkit-input-placeholder {
  color: #6b7374;
}

.login form fieldset input[type="text"]:-moz-placeholder,
.login form fieldset input[type="email"]:-moz-placeholder,
.login form fieldset input[type="password"]:-moz-placeholder {
  /* FF 4-18 */
  color: #000000;
}

.login form fieldset input[type="text"]::-moz-placeholder,
.login form fieldset input[type="email"]::-moz-placeholder,
.login form fieldset input[type="password"]::-moz-placeholder {
  /* FF 19+ */
  color: #000000;
}

.login form fieldset input[type="text"]:-ms-input-placeholder,
.login form fieldset input[type="email"]:-ms-input-placeholder,
.login form fieldset input[type="password"]:-ms-input-placeholder {
  /* IE 10+ */
  color: #000000;
}

.login form fieldset input[type="email"]:focus,
.login form fieldset input[type="text"]:focus,
.login form fieldset input[type="password"]:focus,
.login form fieldset select:focus {
  border: 1px solid #96CDCD;
  background: #ffffff;
  box-shadow: 0 0 5px #51cbee;
}

.login .radio-box {
  margin-top: 1rem;
}

.login form fieldset input[type="radio"] {
  width: auto;
  padding: 0.5rem;
  padding-left: 0;
}

.login form fieldset input[type="submit"] {
  margin-top: 0.5rem;
  padding: 0.5rem 1rem 0.5rem 1rem;
  background: #C0D9D9;
  border: 1px solid #96CDCD;
  cursor: pointer;
}

.login form fieldset input[type="submit"]:hover {
  background: lightskyblue;
}

.login-failed {
  color: firebrick;
  text-align: center;
}

