.popup-form-container {
  z-index: 100;
  position: absolute;
  height: auto;
  width: auto;
  top: 10%;
  left: 50%;
}

.popup-form {
  display: none;
  transform: translateX(-50%);
  background-color: #F9F9F9;
  border: 1px solid #D1D1D1;
  border-radius: 4px;
  -webkit-box-shadow: 0px 7px 5px -5px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 7px 5px -5px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 7px 5px -5px rgba(0, 0, 0, 0.75);
}

.popup-form h1 {
  padding: 1rem;
  font-weight: bold;
  border-bottom: 1px solid #D1D1D1;
}

.popup-form h2 {
  padding: 0.25rem 0.25rem 0.5rem 0.25rem;
  border-top: 1px solid #6b7374;
}

.popup-form-section {
  padding: 0.5rem 0 0.5rem 1rem;
}

.popup-form-close {
  float: right;
  margin-top: -10px;
  margin-right: -10px;
  line-height: 0.5rem;
  padding: 0.25rem 0.5rem 0.75rem 0.5rem;
  cursor: pointer;
  color: #ffffff;
  border-radius: 30px;
  background: #404040;
  font-size: 30px;
  font-weight: bold;
  text-decoration: none;
}

.popup-form-close:before {
  content: "×";
}

.popup-form-close:hover {
  background: lightskyblue;
}

.popup-form form {
  background: #E0EEEE;
  border: 1px solid #96CDCD;
}

.popup-form form fieldset {
  padding: 1rem 2rem 1rem 1rem;
}

.popup-form form fieldset legend {
  padding: 0.5rem 0 0 0.5rem;
  font-size: 80%;
}

.popup-form form fieldset label {
  display: block;
  width: 17.2rem;
  padding: 0.3rem;
  font-size: 80%;
  background: #C4D5DB;
  border-bottom: 1px solid #9CAAAF;
}

.popup-form form fieldset label span {
  color: red;
  padding-left: 0.2rem;
}

.popup-form form fieldset input[type="checkbox"] {
  margin: 0.25rem;
}

.checkbox-field {
  width: 17.25rem;
  margin-left: 0.25rem;
  display: block;
  background: #C0D9D9;
  border: 1px solid transparent;
}

.checkbox-field span {
  margin-right: 0.2rem;
  font-size: 90%;
}

.popup-form form fieldset input[type="checkbox"] {
  transform: scale(1.2);
}

.popup-form form fieldset input[type="email"],
.popup-form form fieldset input[type="text"],
.popup-form form fieldset input[type="password"] {
  width: 16.2rem;
  display: block;
  margin: 0 0 0.25rem 0.25rem;
  padding: 0.5rem;
  background: #C0D9D9;
  border: 1px solid #C4D5DB;
}

.popup-form form fieldset input[type="email"]::-webkit-input-placeholder,
.popup-form form fieldset input[type="text"]::-webkit-input-placeholder,
.popup-form form fieldset input[type="password"]::-webkit-input-placeholder {
  color: #6b7374;
}

.popup-form form fieldset input[type="email"]:-moz-placeholder,
.popup-form form fieldset input[type="text"]:-moz-placeholder,
.popup-form form fieldset input[type="password"]:-moz-placeholder {
  /* FF 4-18 */
  color: #000000;
}

.popup-form form fieldset input[type="email"]::-moz-placeholder,
.popup-form form fieldset input[type="text"]::-moz-placeholder,
.popup-form form fieldset input[type="password"]::-moz-placeholder {
  /* FF 19+ */
  color: #000000;
}

.popup-form form fieldset input[type="email"]:-ms-input-placeholder,
.popup-form form fieldset input[type="text"]:-ms-input-placeholder,
.popup-form form fieldset input[type="password"]:-ms-input-placeholder {
  /* IE 10+ */
  color: #000000;
}

.popup-form form fieldset select {
  width: 17.35rem;
  display: block;
  margin: 0 0 0.25rem 0.25rem;
  padding: 0.5rem;
  background: #C0D9D9;
  border: 1px solid #C4D5DB;
}

option:first-child {
  color: #ccc;
}

.popup-form form fieldset input[type="email"]:focus,
.popup-form form fieldset input[type="text"]:focus,
.popup-form form fieldset input[type="password"]:focus,
.popup-form form fieldset select:focus {
  border: 1px solid #96CDCD;
  background: #ffffff;
  box-shadow: 0 0 5px #51cbee;
}

.popup-form form fieldset input[type="submit"] {
  margin-top: 0.5rem;
  padding: 0.5rem 1rem 0.5rem 1rem;
  background: #C0D9D9;
  border: 1px solid #96CDCD;
  cursor: pointer;
}

.popup-form form fieldset input[type="submit"]:hover {
  background: lightskyblue;
}

.input-error {
  border-color: red !important;
}

.ui-tooltip, .arrow:after {
  background: #ffffff;
  border: 2px solid white;
}

.ui-tooltip {
  padding: 10px 20px;
  color: white;
  border-radius: 20px;
  font-size: 14px;
  box-shadow: 0 0 7px black;
}

.ui-tooltip-content {
  font-size: 80%;
}

.arrow {
  width: 70px;
  height: 16px;
  overflow: hidden;
  position: absolute;
  left: 50%;
  margin-left: -35px;
  bottom: -16px;
}

.arrow.top {
  top: -16px;
  bottom: auto;
}

.arrow.left {
  left: 20%;
}

.arrow:after {
  content: "";
  position: absolute;
  left: 20px;
  top: -20px;
  width: 25px;
  height: 25px;
  box-shadow: 6px 5px 9px -9px black;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.arrow.top:after {
  bottom: -20px;
  top: auto;
}

